function Square(props) {
  const style = {
    margin: '0.5em',
    paddingLeft: 0,
    listStyle: 'none',
    width:'100px',
    height:'100px'
  };
  return (
    <button style={style} className="square" onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {
  

  renderSquare(i) {
    return (
      <Square
        value={this.props.squares[i]}
        onClick={() => this.props.onClick(i)}
      />
    );
  }

  render() {
    const vieja = {float: 'left',width: '300px',display:'block'};
    const row = {float: 'left',width: '100%'};
    const col = {float: 'left',width: '33.33%'};
    return (
      <div style={vieja}>
        <div style={row} className="board-row">
        <div style={col}>{this.renderSquare(0)}</div>
        <div style={col}>{this.renderSquare(1)}</div>
        <div style={col}>{this.renderSquare(2)}</div>
        </div>
        <div style={row} className="board-row">
          <div style={col}>{this.renderSquare(3)}</div>
          <div style={col}>{this.renderSquare(4)}</div>
          <div style={col}>{this.renderSquare(5)}</div>
          
          
          
        </div>
        <div style={row} className="board-row">
        <div style={col}>{this.renderSquare(6)}</div>
        <div style={col}>{this.renderSquare(7)}</div>
        <div style={col}>{this.renderSquare(8)}</div>
          
          
          
        </div>
      </div>
    );
  }
}
  
  class Game extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        history: [{
          squares: Array(9).fill(null),
        }],
        stepNumber: 0,
        xIsNext: true,
      };
    }

    handleClick(i) {
      const history = this.state.history.slice(0, this.state.stepNumber + 1);
      const current = history[history.length - 1];
      const squares = current.squares.slice();
      if (calculateWinner(squares) || squares[i]) {
        return;
      }
      squares[i] = this.state.xIsNext ? 'X' : 'O';
      this.setState({
        history: history.concat([{
          squares: squares
        }]),
        stepNumber: history.length,
        xIsNext: !this.state.xIsNext,
      });
    }


    jumpTo(step) {
      this.setState({
        stepNumber: step,
        xIsNext: (step % 2) === 0,
      });
    }

    render() {
      const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = calculateWinner(current.squares);
    const row = {float: 'left',width: '100%'};
    const col = {float: 'left',width: '33.33%'};
      const moves = history.map((step, move) => {
        const desc = move ?
          'Go to move #' + move :
          'Go to game start';
        return (
          <li key={move}>
            <button onClick={() => this.jumpTo(move)}>{desc}</button>
          </li>
        );
      });
  
      let status;
      if (winner) {
        status = 'Winner: ' + winner;
      } else {
        status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
      }
  
      return (
        <div className="game">
          <div className="game-board">
            <Board
              squares={current.squares}
              onClick={(i) => this.handleClick(i)}
            />
          </div>
          <div style={row} className="game-info">
            <div>{status}</div>
            <ol>{moves}</ol>
          </div>
        </div>
      );
    }
  }
  
  // ========================================
  export default  Game;
